import { Component, OnInit, ContentChild, HostBinding, Input} from '@angular/core';
import { FooterHeaderDirective} from './directive/footer-header.directive';
import { FooterHeaderToolbarExtendDirective } from './directive/footer-header-toolbar-extend.directive';
import { FooterHeaderContentDirective } from './directive/footer-header-content.directive';
import { FooterHeaderToolbarDirective } from './directive/footer-header-toolbar.directive';
import { LocaleService } from '@farris/ui-locale';
@Component({
  selector: 'farris-footer',
  template: `
   <footer class="f-cmp-footer-container">
      <ng-container *ngIf="showHeader&&headerDirective">
          <header class="f-cmp-footer-header" [ngClass]="headerDirective.extendCls">
              <ng-template  [ngTemplateOutlet]="headerDirective.templateRef"></ng-template> 
          </header>
      </ng-container>
      <ng-container *ngIf="showHeader&&!headerDirective">
        <header class="f-cmp-footer-header" [ngClass]="headerCls">
          <div class="f-content" *ngIf="headerContentDirective" [ngClass]="headerContentDirective.extendCls">
            <ng-template [ngTemplateOutlet]="headerContentDirective.templateRef"></ng-template> 
          </div>
          <ng-container *ngIf="headerToolbarDirective">
              <div class="f-toolbar" [ngClass]="headerToolbarDirective.extendCls">
                  <ng-template  [ngTemplateOutlet]="headerToolbarDirective.templateRef"></ng-template> 
              </div>
          </ng-container>
          <div class="f-toolbar" *ngIf="!headerToolbarDirective">
            <ng-template *ngIf="headerToolbarExtendDirective" [ngTemplateOutlet]="headerToolbarExtendDirective.templateRef"></ng-template> 
            <div class="toolbar--collapse" *ngIf="enableAccordion" (click)="expadnOrCollapse($event)">
                <span class="toobar--collapse-text">
                   <ng-container  *ngIf="collapseStatus">{{textCollapse}}</ng-container>
                   <ng-container  *ngIf="!collapseStatus">{{textExpand}}</ng-container>
                </span>              
                <i class="f-icon toolbar--collapse-icon"></i>
            </div>
          </div>
        </header>
      </ng-container> 
      <div class="f-cmp-footer-content" [ngClass]="contentCls">
        <ng-content></ng-content>
      </div>
   </footer>
  `,
  styles: []
})
export class FooterComponent implements OnInit {
  @HostBinding('class.f-cmp-footer') cls = true;
  // å±å¼
  @HostBinding('class.f-state-collapse') get getCollapseCls(): boolean {
    if (this.enableAccordion) {
      return this.collapseStatus;
    } else {
      return false;
    }
  }
  // å¯ç¨å±å¼æ¶èµ·
  @HostBinding('class.f-cmp-footer-accordion') get enableAccordionCls(): boolean {
    return this.enableAccordion;
  }
  textCollapse=this.localeService.getValue('footer.collapseText');
  // æ¶èµ·æå­
  @Input() 
  set collapseText(value){
    if(value){
      this.textCollapse=value;
    }
  } 
  get collapseText(){
    return this.textCollapse;
  }
  textExpand= this.localeService.getValue('footer.expandText');
  // å±å¼æå­
  @Input()
  set expandText(value){
    if(value){
      this.textExpand=value;
    }
  } 
  get expandText(){
    return this.textExpand;
  }
  // æ¾ç¤ºå¤´é¨åºå
  @Input() showHeader = true;
  // æ²¡æç¨æ¨¡æ¿åç¬å®ä¹class
  @Input() headerCls = "";
  // åç¬å®ä¹ä¸»åå®¹åºçclass
  @Input() contentCls = "";
  // å±å¼ç¶æ
  @Input() collapseStatus = true;
  // æ¯å¦å¯ç¨å±å¼æ¶èµ·
  @Input() enableAccordion = true;
  // å¤´é¨æ¨¡æ¿
  @ContentChild(FooterHeaderDirective) headerDirective: any;
  // å¤´é¨ä¸»åå®¹æ¨¡æ¿
  @ContentChild(FooterHeaderContentDirective) headerContentDirective: any;
  // Header Toolbaræ©å±æ¨¡æ¿
  @ContentChild(FooterHeaderToolbarExtendDirective) headerToolbarExtendDirective: any;
  // Header Toolbaræ¨¡æ¿
  @ContentChild(FooterHeaderToolbarDirective) headerToolbarDirective: any;
  constructor(private localeService: LocaleService) { }

  ngOnInit() {

  }
  // æ¶èµ·è¿æ¯å±å¼
  expadnOrCollapse(ev: Event) {
    ev && ev.stopImmediatePropagation();
    this.collapseStatus = !this.collapseStatus;
  }
}
